
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #235;
  margin-top: 60px;
}
#app .menuBar {
    background-color: transparent;
    border: none;
    outline: none;
    padding: 0;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    background-color: #235;
}
#app .menuBarhober {
      background-color: #123;
}

button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  font-weight: inherit;
}
.container {
  margin-bottom: 5%;
}
.container input[type="text"] {
    margin: 1% 0 2% 0;
}
.container input[type="button"] {
    width: 10%;
    margin: 0.5%;
    -webkit-box-shadow: 0.5% 0.1% #ccc;
            box-shadow: 0.5% 0.1% #ccc;
}
#todo {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
#todo .todoList {
    margin: 0 5%;
}
#todo .todoList tr {
      width: auto;
}
#todo .todoList th {
      cursor: pointer;
}
#todo .todoList .select {
      width: 20%;
}
#todo .todoList .select label {
        display: block;
}
#todo .todoList .select td input {
        cursor: pointer;
        background-color: transparent;
        border: none;
        outline: none;
        padding: 0;
        -webkit-appearance: none;
           -moz-appearance: none;
                appearance: none;
}
#todo .todoList .name {
      width: 40%;
}
#todo .todoList .name input {
        cursor: pointer;
        background-color: transparent;
        border: none;
        outline: none;
        padding: 0;
        -webkit-appearance: none;
           -moz-appearance: none;
                appearance: none;
}
#todo .todoList .status {
      width: 20%;
}
#todo .todoList .status input {
        cursor: pointer;
        background-color: transparent;
        border: none;
        outline: none;
        padding: 0;
        -webkit-appearance: none;
           -moz-appearance: none;
                appearance: none;
}
#todo .todoList .drag {
      display: table-row;
      width: auto;
}
#todo .todoList .drag .record :hover {
        background-color: #eee;
}
#todo .todoList th {
      padding: 10px;
      border-bottom: solid 2px #ccc;
}
#todo .todoList td {
      padding: 5px;
      border-bottom: solid 1px #ccc;
}
